ip-to-content {\n  position: absolute;\n  top: -100px;\n  left: 0;\n  background-color: $color-1;\n  color: $color-2;\n  padding: 20px;\n\n  &:focus {\n    top: 0;\n  }\n}\n\n/* \n *  APP-BAR\n*/\n.app-bar {\n  .app-bar__inner {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    width: 90%;\n    margin: auto;\n    padding: 25px 0;\n  }\n  .app-bar__menu {\n    justify-content: center;\n    font-size: 1.3rem;\n  }\n  .app-bar__brand a {\n    color: $color-1;\n    font-size: 1.8rem;\n\n    i {\n      margin-right: 10px;\n    }\n    &hover {\n      opacity: unset;\n    }\n  }\n}\n.nav {\n  display: none;\n\n  .nav__list {\n    display: flex;\n    column-gap: 50px;\n    list-style-type: none;\n  }\n\n  .nav__item a {\n    justify-content: center;\n  }\n}\n\n\n/* \n *  HERO\n*/\n.hero {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  text-align: center;\n  background-image: url(../public/images/heros/hero-image_3.jpg);\n  background-size: cover;\n  background-position: center;\n  color: $color-2;\n  min-height: 350px;\n\n  .hero__inner {\n    width: 90%;\n    margin: auto;\n    background-color: rgb(0, 0, 0, 0.5);\n    max-width: 700px;\n  }\n  .hero__title {\n    padding: 20px;\n  }\n}\n\n\n/* \n *  MAIN\n*/\nmain {\n  padding: 70px 0;\n}\n\n/* \n *  .RESTAURANT\n*/\n.sort {\n  margin-bottom: 20px;\n\n  .sort__title {\n    margin-right: 8px;\n  }\n  .sort__input, .sort__option {\n    padding: 5px 3px;  \n  }\n}\n\n.resto {\n  margin-bottom: 80px;\n\n  .resto__inner {\n    width: 90%;\n    margin: auto;\n  }\n  .resto__title {\n    font-size: 1.3rem;\n    margin-bottom: 15px;\n  }\n  .resto__list {\n    display: grid;\n    gap: 25px 15px;\n  }\n  .resto__item {\n    position: relative;\n    box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n    overflow: hidden;\n  }\n  .resto__picture {\n    width: 100%;\n    max-height: 250px;\n    object-fit: cover;\n  }\n  .resto__info {\n    padding: 1.5rem 1.3rem;\n    padding-bottom: 2.2rem;\n  }\n  .resto__city {\n    position: absolute;\n    top: 15px;\n    left: 0;\n    background-color: $color-3;\n    color: $color-2;\n    padding: 5px 15px;\n    border-radius: 0 3px 3px 0;\n  }\n  .resto__star {\n    font-size: 1rem;\n    margin-bottom: 5px;\n  }\n  .resto__star i {\n    color: #c6cc14;\n    margin-right: 7px;\n  }\n  .resto__name a {\n    font-size: 1.2rem;\n    margin-bottom: 5px;\n  }\n  .resto__name a:hover {\n    opacity: 0.8;\n  }\n  .resto__description {\n    padding-right: 20px;\n    font-size: 0.85rem;\n    line-height: 1.4rem;\n  }\n}\n\n/* \n *  TODAY'S MENU\n*/\n.todays-menu {\n  .todays-menu__inner {\n    width: 90%;\n    margin: auto;\n  } \n\n  .todays-menu__title {\n    font-size: 1.3rem;\n    margin-bottom: 20px;\n  }\n  .todays-menu__figure {\n    width: 100%;\n  }\n  .todays-menu__figure img {\n    width: 100%;\n    object-fit: cover;\n  }\n  .todays-menu__figure figcaption {\n    text-align: center;\n    font-size: .85rem;\n    margin-top: 8px;\n  }\n  .todays-menu__info {\n    padding: 20px;\n\n    .todays-menu__name  {\n      font-size: 1.2rem;\n      margin: 8px 0;\n    }\n    .todays-menu__recipe, .todays-menu__instruction {\n      border-top: 2px solid $color-3;\n      margin-top: 20px;\n    }\n    ul {\n      margin-left: 10px;\n    }\n    li {\n      margin-bottom: 5px;\n      font-size: 0.85rem;\n      line-height: 1.4rem;\n    }\n    h5 {\n      font-size: 1rem;\n      margin: 10px 0;\n    }\n  }\n}\n\n\n/* \n *  FOOTER\n*/\n.footer {\n  background-color: $color-1;\n  color: $color-2;\n  \n  .footer__inner {\n    width: 90%;\n    margin: auto;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    font-size: 0.9rem;\n    padding: 30px 0;\n    text-align: center;\n  }\n\n  .back-to-top {\n    background-color: transparent;\n    border: none;\n    span {\n      font-size: 1.1rem;\n      margin-right: 7px;\n    }\n  }\n}\n\n/* Responsive */\n@media screen and (max-width: 600px) {\n  .footer__inner {\n    flex-direction: column;\n    gap: 10px;\n  }\n}\n@media screen and (min-width: 600px) {\n  .resto__list {\n    grid-template-columns: repeat(2, 1fr);\n  }\n}\n@media screen and (max-width: 768px) {\n  .app-bar__brand {\n    font-size: 1.5rem;\n  }\n  .app-bar__inner {\n    position: relative;\n  }\n  .app-bar__menu {\n    transition: 0.3s;\n  }\n\n  .nav {\n    display: block;\n    color: $color-2;\n    background-color: $color-1;\n    text-align: center;\n    border-radius: 3px;\n    padding: 40px 45px;\n    position: absolute;\n    right: 0;\n    top: 70px;\n    -webkit-transform: translate(300px, 0);\n    transform: translate(300px, 0);\n    opacity: 0;\n    transition: transform 0.3s, opacity 1s;\n  }\n  .nav__list {\n    flex-direction: column;\n    row-gap: 15px;\n  }\n\n  .open {\n    display: block;\n    -webkit-transform: translate(0, 0);\n    transform: translate(0, 0);\n    opacity: 1;\n  }\n  .close {\n    color: crimson;\n    transform: rotate(90deg);\n  }\n\n  .resto__description {\n    font-size: 0.85rem;\n  }\n}\n@media screen and (min-width: 769px) {\n  .app-bar__menu {\n    display: none;\n  }\n  .nav {\n    display: block;\n  }\n}\n@media screen and (min-width: 830px) {\n  .todays-menu__figure {\n    text-align: center;\n  }\n  .todays-menu__figure img {\n    max-height: 400px;\n  }\n}\n@media screen and (min-width: 1000px) {\n  .todays-menu__info {\n    display: grid;\n    grid-template-columns: 1fr 1fr;\n  }\n  .todays-menu__name {\n    grid-column-start: 1;\n    grid-column-end: 3;\n  }\n}\n@media screen and (min-width: 1200px) {\n  .resto__list {\n    grid-template-columns: repeat(3, 1fr);\n  }\n}\n"],"sourceRoot":""}]);
